
.square{
  float: left;
}
.square-red{
  background-color: red;
  border-radius: 50%;
  
  border-bottom: 3px solid black;
}
.square-orange{
  
  border-radius: 50%;
  background-color: orange;
  
  border: 3px solid black;
}
.square-border{
  border-radius: 50%;
  border-bottom: 10px solid;
  border-top: 4px solid;
  border-right: 4px solid;
  border-left: 4px solid;
}
.border-orange{
  border-color: orange;
}

.border-yellow{
  border-color: yellow;
}

.border-green{
  border-color: green;
}

.border-blue{
  border-color: blue;
}

.border-red{
  border-color: red;
}
.square-green{
  
  border-radius: 50%;
  background-color: green;
  border-bottom: 3px solid black;
}
.square-yellow{
  
  border-radius: 50%;
  background-color: yellow;
  border-bottom: 3px solid black;
}
.square-blue{
  
  border-radius: 50%;
  background-color: blue;
  border-bottom: 3px solid black;
}
.square-outer{
  border-radius: 50%;
  border-bottom: 5px solid white;

}
.square-white{
  
  border-radius: 50%;
  float:none;
  border: 3px solid black;
  background-color: white;
}

.bg-white{
  background-color: white;
}
.trapezoid {
  position: absolute;
  left: 25vw;
  top:100px;
  border-bottom: 30vw solid grey;
  border-left: 20vw solid transparent;
  border-right: 20vw solid transparent;
  height: 0;
  z-index: -1;
}
.t-black{
  border-bottom-color: black;
  z-index: -2;
  border-bottom-width: calc(30vw + 5px);
  left: calc(25vw - 5px);
}

img{
  position:absolute;
  top:0;
  left:calc(50vw - 140px);
}

#background{
  position:absolute;
  background: linear-gradient(to bottom, #fffb00, #ffa247);
  z-index: -10;
}

audio{
  position:absolute;
  z-index: 100;
}